
.row--project {
  margin-left: -14px;
  margin-right: -14px;
  & > .col-12 {
    padding-left: 14px;
    padding-right: 14px;
  }
}

.project-slider {
  margin: -24px -24px 24px -24px;
  @media (max-width:$md) {
    margin: -16px -16px 16px -16px;
  }
  .swiper-slide {
    max-height: 440px;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    &:before {
      content: '';
      display: block;
      padding-top: 40%;
    }
  }
  @media (max-width:$md) {
    .swiper-button {
      width: 24px;
      height: 24px;
      margin-top: -12px;
      font-size: 12px;
    }
  }
}

.project-data {
  margin-bottom: 28px;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  li {
    margin: 0 40px 12px 0;
    font-weight: 500;
    line-height: 16px;
    @media (max-width:$md) {
      font-weight: 400;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &_note {
      display: block;
      font-size: 13px;
      line-height: 20px;
      color: $black-400;
      margin-top: 4px;
      font-weight: 400;
  }
  @media (max-width: $nodesk) {
    display: block;
    margin-bottom: 16px;
    li {
      margin: 0 0 8px 0;
      font-size: 13px;
      line-height: 20px;
      display: flex;
      justify-content: space-between;
      flex-direction: row-reverse;
    }
    .project-data_note {
      color: $black;
      margin: 0;
      padding-right: 8px;
    }
  }
}

.nav-tabs--condition {
  @media (min-width:$md) {
    margin-top: -52px;
    width: max-content;
    float: right;
  }
}

.projects-chart {
  width: 220px;
  margin-right: 40px;
  &__sum {
    z-index: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    white-space: nowrap;
    font-weight: 500;
    font-size: 26px;
    line-height: 32px;
    pointer-events: none;
    @include transition;
    span {
      display: block;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: $black-400;
    }
    &-unl {
      font-size: 50px;
    }
  }
  &:hover {
    .projects-chart__sum {
      opacity: 0;
      pointer-events: none;
    }
  }
  @media (max-width:991px) {
    margin: 0 auto 24px auto
  }
}


