
.project-preview {
  padding: 24px 16px 20px 16px;
  min-height: 100%;
  background: $white;
  box-shadow: $shadow-small;
  border-radius: 8px;
  border: 1px solid $white;
  display: flex;
  flex-direction: column;
  &__time {
    margin-bottom: 12px;
    display: inline-block;
    width: max-content;
    z-index: 1;
    padding: 2px 12px;
    border-radius: 4px;
    background-color: $primary-200;
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    color: $primary-900;
    span {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      z-index: 0;
      width: 0;
      opacity: .12;
    }
  }
  h2, .h2 {
    width: 100%;
    margin-bottom: 24px;
  }
  &__body {
    width: 100%;
    margin-bottom: 16px;
    @include horizonCenter;
  }
  &__chart {
    width: 100px;
    height: 100px;
    border-radius: 60%;
    overflow: hidden;
  }
  &__info {
    width: calc(100% - 100px);
    padding-left: 16px;
    ul {
      padding: 0;
      margin: -4px -16px;
      display: flex;
      flex-wrap: wrap;
      li {
        padding: 4px 16px;
        color: $black-400;
        font-size: 13px;
        font-weight: 400;
        span {
          margin-bottom: 4px;
          font-size: 16px;
          line-height: 16px;
          font-weight: 500;
          color: $black-900;
          display: block;
          white-space: nowrap;
        }
      }
    }
  }
  &__sum {
    font-weight: 500;
    margin-bottom: 12px;
    .sep {
      margin: 0 4px;
    }
    &_total {
      display: inline-block;
      font-size: 20px;
      line-height: 20px;
      white-space: nowrap;
    }
    &_actual {
      display: inline-block;
      white-space: nowrap;
      line-height: 16px;
    }
  }
  .btn {
    margin: auto auto 0;
    font-size: 16px;
  }

  &--ends {
    border-color: $red-900;
    .project-preview__time {
      color: $red-900;
      background-color: $red-100;
      span {
        background: $red-900;
      }
    }
    .project-preview__info {
      ul {
        li {
          font-weight: 700;
        }
      }
    }
    .ldBar__mask,
    .ldBar-label {
      font-weight: 700;
    }
    .btn {
      font-weight: 700;
    }
  }

  &--finished {
    .project-preview__time {
      span {
        display: none;
      }
      background-color: $green-100;
      color: $green-900;
    }
    .ldBar path.mainline {
      stroke: $green-900;
    }
  }
  &--unlimited {
    .ldBar__mask {

    }
    .ldBar-label {
      font-size: 0;
      bottom: 36px;
      &:after {
        content: '~';
        font-size: 20px;
      }
    }
  }
}

@media (max-width: $md) {
  .project-preview {
    padding: 16px;
    display: block;
    .btn {
      position: absolute;
      margin: 0;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      opacity: 0;
      z-index: 10;
      border-radius: 0;
    }
    &__time {
      margin-bottom: 10px;
    }
    h2, .h2 {
      font-size: 20px;
      margin-bottom: 12px;
    }
    &__body {
      display: block;
      margin-bottom: 0;
    }
    &__info {
      width: 100%;
      padding: 0;
      display: flex;
      flex-direction: column-reverse;
    }
    &__chart {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 64px;
      height: 64px;
    }
    &__sum {
      width: 100%;
      margin: 32px 0 0 0;
      padding: 0 0 0 80px;
      min-height: 64px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      align-content: center;
      &:before {
        content: '';
        position: absolute;
        top: -16px;
        left: -16px;
        right: -16px;
        height: 1px;
        background: $primary-300;
      }
    }
    .ldBar__mask {
      display: none;
    }
    .ldBar-label {
      bottom: 20px;
      font-size: 16px;
    }
  }
}
