
.step {
  width: 100%;
  display: flex;
  &-item {
    padding: 0 2px;
    flex-grow: 1;
    text-align: center;
    &:before, &:after {
      content: '';
      position: absolute;
      top: 15px;
      height: 2px;
      width: 50%;
      z-index: 0;
      border-top: 2px dotted #DADDE4;
    }
    &:before {
      left: 1px;
    }
    &:after {
      right: 1px;
    }
    &__count {
      @include verticalCenter;
      border-radius: 50%;
      color: $white;
      font-weight: 500;
      background-color: $black-400;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 16px auto;
      width: 32px;
      height: 32px;
      margin: 0 auto 8px auto;
      z-index: 1;
      @include transition;
    }
    &__name {
      font-size: 13px;
      font-weight: 500;
      color: $black-500;
      @media (max-width: $sm) {
        font-size: 11px;
      }
    }

    &:first-child {
      &:before {
        display: none;
      }
    }
    &:last-child {
      &:after {
        display: none;
      }
    }

    &.past {
      &:before, &:after {
        border-top-style: solid;
        border-top-color: $primary-900;
      }
      &:before {
        left:0px;
      }
      &:after {
        right: 0px;
      }
      .step-item__count {
        background-color: $primary-900;
        font-size: 0;
        background-image: url(../img/icons/icon-check.svg);
      }
      .step-item__name {
        color: $primary-900;
      }
    }
    &.present {
      &:before {
        border-top-style: solid;
        border-top-color: $primary-900;
      }
      &:before {
        left: 0px;
      }
      &:after {
        right: 0px;
      }
      .step-item__count {
        background-color: $primary-900;
      }
      .step-item__name {
        color: $primary-900;
      }
    }
  }

}
