
//fancybox

.fancybox-navigation {
  position: static;
}
.fancybox-is-open .fancybox-bg {
  opacity: .5;
}
.fancybox-slide--html {
  padding: 0;
  .fancybox-close-small {
    @media (max-width:$md) {
      //display: none;
      pointer-events: none;
      top: 0;
      right: 0;
      padding: 0;
      left: 0;
      display: block;
      width: 100%;
      bottom: auto;
      height: 24px;
      svg {
        display: none;
      }
      &:before {
        content: '';
        position: absolute;
        top: 12px;
        width: 80px;
        height: 4px;
        background: $black-300;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 4px;
      }
    }
  }
}
.fancybox-slide.fancybox-slide--html {
  @media (max-width: $md) {
    &:before {
      vertical-align: bottom;
    }
    .my-modal {
      width: 100%;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }
}

//swiper

.swiper-button {
  outline: none;
  width: 40px;
  height: 40px;
  background: $white-900;
  box-shadow: $shadow-XLarge;
  font-size: 24px;
  color: $black-500;
  @include verticalCenter;
  @include transition;
  border-radius: 50%;
  &.swiper-button-prev {

  }
  &:before, &:after {
    display: none;
  }
  &--small {
    width: 28px;
    height: 28px;
  }
  &:hover {
    @media (min-width:$nodesk) {
      color: $primary-950;
    }
  }
  &:focus {
    opacity: 1;
    color: $primary-950;
  }
}

.swiper-pagination {
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 50px;
  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: $white;
    opacity: 1;
    outline: none;
    &.swiper-pagination-bullet-active {
      background-color: $primary-950;
    }
  }
  &--small {
    .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
    }
  }
}

.swiper-container--mob {
  overflow: visible;
  .swiper-slide {
    height: auto;
    width: 240px;
  }
  .swiper-button-disabled {
    opacity: 0;
  }
  @media (min-width: 940px) {
    .swiper-wrapper {
      width: calc(100% + 16px);
      margin: -12px -8px;
      display: flex;
      flex-wrap: wrap;
    }
    .swiper-slide {
      margin: 12px 8px;
    }
    .swiper-button {
      display: none;
    }
  }
}

//nice-select

.form-control.nice-select {
  line-height: 44px;
  padding-right: 40px;
  &.form-control-lg {
    @include media-breakpoint-up(sm) {
      line-height: 50px;
    }
  }
  &.is-invalid {
    color: $black-900;
  }
}
.nice-select {
  float: none;
  padding-right: 35px;
  line-height: 44px !important;
  .current {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    color: $black-400;
    &.current-active {
      color: $black-900;
    }
  }
  &.open {
    .current {
      color: $black-900;
    }
  }
  &:after {
    border-width: 1px;
    border-color: $black-900;
    width: 7px;
    height: 7px;
    right: 20px;
  }
  &.disabled {
    border-color: transparent;
  }
  &:hover {
    border-color: transparent;
  }
  .list {
    width: 100%;
    background-color: $primary-200;
    box-shadow: none;
    .option {
      line-height: 20px;
      font-size: 13px;
      padding: 8px 10px 8px 20px;
      white-space: normal;
      min-height: auto;
      height: auto;
      &:hover {
        background-color: $primary-300;
      }
      &.selected, &.focus {
        background-color: $primary-300;
        color: $primary-900;
      }
    }
  }
  &.select--light {
    background-color: $white;
    box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.07);
    border: none !important;
    &:focus {
      background-color: $white;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.07) !important;
    }
    .list {
      background-color: $white;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.07);
      .option {
        &:hover {
          background-color: $primary-300;
        }
        &.selected, &.focus {
          background-color: $primary-300;
          color: $primary-900;
        }
      }
    }
  }
  &.select-min-width {
    min-width: 240px;
    max-width: 100%;
  }

  @media (max-width: $md-tablet) {
    padding-right: 20px !important;
    &:after {
      display: none;
    }
    .list {
      display: none !important;
    }
  }

}

.select-list {
  display: none;
  @media (max-width: $md-tablet) {
    display: block;
    position: fixed;
    top: auto;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 16px;
    z-index: 16;
    transition: .5s;
    max-height: calc(100vh - 30px);
    overflow: auto;
    background-color: $white;
    padding: 8px 16px 12px 16px;
    border-radius: 8px;
    opacity: 0;
    animation: 1s fadeIn forwards;
    &__button {
      font-weight: 500;
      width: 100%;
      font-size: 13px;
      padding: 4px 0;
      min-height:  44px;
      display: flex;
      align-items: center;
      &:hover {
        color: $primary-900;
      }
    }
    &__close {
      margin-top: 8px;
      width: 100%;
      font-weight: 500;
      line-height: 24px;
      padding: 8px 24px;
      border-radius: 4px;
      box-shadow: none !important;
      outline: none !important;
      @include transition;
      background-color: $primary-200;
      color: $primary-900;
      &:hover {
        color: $white;
        background-color: $primary-700;
      }
      &:not(:disabled):not(.disabled).active,
      &:not(:disabled):not(.disabled):active,
      &:active {
        color: $white;
        background-color: $primary-950;
      }
    }
  }
}

.overlay-select {
  display: none;
  @media (max-width: $md-tablet) {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 15;
    cursor: pointer;
    background: rgba(0,0,0,.5);
    transition: .5s;
    opacity: 0;
    animation: 1s fadeIn forwards;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 1;
  }
}

.fancybox-active {
  .select-list {
    @media (max-width: $md-tablet) {
      z-index: 150001;
    }
  }
  .overlay-select {
    @media (max-width: $md-tablet) {
      z-index: 150000;
    }
  }
}

  //iconmoon

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}


//dropzone

.wrap-dropzone {
  display: flex;
  flex-wrap: wrap;
  & > * {
    margin-right: $indent-sm;
    &:last-child {
      margin-right: 0;
    }
    @media (max-width:399px) {
      margin-bottom: $indent-sm;
      margin-right: 0;
    }
  }
}
.dropzone {
  width: 160px;
  min-height: 124px;
  height: 124px;
  background: $primary-200;
  padding: 0;
  border-radius: 4px;
  border: none !important;
  @include transition;
  @media (max-width:399px) {
    width: 100%;
  }
  &.dz-started {
    opacity: .5;
  }
  *:not(.dz-progress) {
    background: transparent !important;
  }
  &:before {
    content: '';
    position: absolute;
    pointer-events: none;
    z-index: 1100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 4px;
    border: 2px dashed $primary-900;
  }
  .dz-message {
    padding: 12px 8px;
    margin: 0;
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    height: 100%;
    @include verticalCenter;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    .icon {
      font-size: 56px;
      width: 56px;
      height: 56px;
      display: block;
      margin: 0 auto;
      @include transition;
    }
  }
  .dz-preview {
    width: 100%;
    margin: 0;
    min-height: 124px;
    height: 124px;
    border-radius: 4px;
    background: transparent;
    &.dz-image-preview {
      background: transparent;
    }
    .dz-image {
      overflow: hidden;
      border-radius: 4px;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        min-height: 100%;
        object-fit: cover;
        @include transition;
      }
    }
    .dz-progress {
      height: 4px;
      margin-top: -2px;
      background: $primary-900;
    }
    .dz-error-message {
      display: none !important;
      border-radius: 4px;
      font-size: 8px;
      top: 100%;
      transform: translateY(10px);
      left: 0;
      width: 100%;
      background: $red;
      padding: 12px;
      &:after {
        top: -4px;
        left: 50%;
        margin-left: -3px;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-bottom: 4px solid $red;
      }
    }
    .dz-details {
      display: none !important;
      font-size: 12px;
      min-width: 100%;
      max-width: 100%;
      height: 100%;
      padding: 12px 8px;
      text-align: center;
      color: $white;
      background-color: rgba($primary-900,.7);
      @include verticalCenter;
      .dz-size {
        font-size: 14px;
        margin-bottom: 4px;
        span {
          border: none;
          background-color: transparent;
          padding: 0;
          border-radius: 0;
        }
      }
      .dz-filename {
        span {
          border: none;
          background-color: transparent;
          padding: 0;
          border-radius: 0;
        }
        &:hover span {
          border: none;
          background-color: transparent;
        }
      }
    }
    .dz-success-mark g path {
      fill: #32af55
    }
    .dz-error-mark g {
      fill: $red-900;
    }
    .dz-success-mark, .dz-error-mark {
      margin-left: -8px;
      margin-top: -8px;
      cursor: pointer;
      pointer-events: auto;
      & > * {
        cursor: pointer;
      }
      svg {
        width: 16px;
        height: 16px;
      }
    }
    &:hover .dz-image img {
      transform: none;
      filter: none;
    }
  }

  &.dropzone-lg {
    width: 100%;
    height: 208px;
    .dz-preview {
      min-height: 208px;
      height: 208px;
      @media (max-width:$md) {
        height: 144px;
      }
    }
    @media (max-width:$md) {
      height: 144px;
    }
  }



  &:hover {
    .dz-message {
      .icon {
        transform: translateY(5px);
      }
    }
  }
}

.dropzone--false {
  height: 208px;
  background: $primary-200;
  border: 1px solid $primary-900;
  border-radius: 4px;
  padding: 24px 16px;
  font-size: 13px;
  font-weight: 500;
  @include verticalCenter;
  .icon {
    color: $primary-900;
    width: 56px;
    height: 56px;
  }
  @media (max-width:$md) {
    height: 144px;
  }
}

//loading-bar

.ldBar {
  pointer-events: none;
  width: 100% !important;
  height: 100% !important;
  position: relative;
  svg {
    overflow: visible !important;
  }
  .label-center {

  }
  .ldBar-label {
    white-space: nowrap;
    font-size: 20px;
    font-weight: 500;
    position: absolute;
    bottom: 44px;
    left: 0;
    right: 0;
    text-align: center;
    &:after {
      content: "%";
      display: inline;
    }
  }
  &.no-percent {
    .ldBar-label {
      &:after {
        content: "";
      }
    }
  }

  &__mask {
    position: absolute;
    bottom: 28px;
    left: 0;
    right: 0;
    z-index: 1;
    text-align: center;
    font-size: 13px;
    font-weight: 400;
    color: $black-400;
    line-height: 20px;
  }
  path.mainline {
    stroke-width: 6;
    stroke: $primary-900;
    stroke-linecap: round;
  }
  path.baseline {
    stroke-width: 6;
    stroke: $primary-200;
    stroke-linecap: round;
  }
  &.ldBar--ready {
    path.mainline {
      stroke: $green;
    }
  }
}


//tooltip

.tooltip-info {
  cursor: pointer;
  color: $primary-900;
  @include transition;
  vertical-align: middle;
  margin-top: -2px;
  font-size: 24px;
  svg {
    pointer-events: none;
  }
  &:hover {
    opacity: .7;
  }
}
.tooltip-inner {
  box-shadow: 0px 4px 12px 0 rgba($black,.15);
}
.tooltip-arrow {
  z-index: 1;
}

.modal-tooltip {
  display: none;
  max-width: 100%;
  background-color: #fff;
  overflow: visible;
  box-shadow: $shadow-medium;
  padding: 32px;
  width: 480px;
  border-radius: 8px;
  .fancybox-close-small {
    display: none;
  }
  @media (max-width: $md-tablet) {
    padding: 16px;
  }
  &__text {
    font-size: 13px;
    line-height: 20px;
    color: $black-500;
  }
  .btn {
    margin-top: 20px;
    @media (max-width: $md-tablet) {
      margin-top: 8px;
    }
  }
}
.fancybox-tooltip {
  .fancybox-slide {
    padding: 16px;
  }
}
@media (max-width: $nodesk) {
  .modal-tooltip {
    transform: translateY(100%);
    opacity: 0;
    -webkit-transition: opacity .5s, -webkit-transform .5s;
    transition: opacity .5s, -webkit-transform .5s;
    -o-transition: opacity .5s, -o-transform .5s;
    -moz-transition: transform .5s, opacity .5s, -moz-transform .5s;
    transition: transform .5s, opacity .5s;
    transition: transform .5s, opacity .5s, -webkit-transform .5s, -moz-transform .5s, -o-transform .5s;
  }
  .fancybox-slide--current .modal-tooltip {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: $md-tablet) {
  .tooltip {
    display: none !important;
  }
}


//nouislider

.noUi-tooltip {
  display: none;
}
.noUi-active .noUi-tooltip {
  display: block;
}
.custom-range-slider {

}
.custom-range-slider__track {
  margin-top: 24px;
  margin-bottom: 8px;
  background: $gradient-800;
  border-radius: 4px;
  border: none;
  box-shadow: none;
}
.custom-range-slider__data {
  margin-bottom: 24px;
  height: 20px;
  @include horizonCenterBetween;
  span {
    white-space: nowrap;
    font-size: 13px;
    color: $black-400;
    position: absolute;
    top: 0;
    &:before {
      content: '';
      position: absolute;
      bottom: 100%;
      width: 2px;
      left: 0;
      height: 8px;
      margin-bottom: 8px;
      background-color: $white;
      z-index: 1;
    }
    &:nth-child(1) {
      &:before {
        display: none;
      }
    }
  }
}
.noUi-horizontal {
  height: 8px;
}
.noUi-connect {
  background-color: $primary-700;
}
.noUi-handle {
  width: 24px;
  height: 24px;
  background: $primary-700;
  border: 4px solid $white;
  box-sizing: border-box;
  box-shadow: $shadow-XXLarge;
  border-radius: 50%;
  cursor: pointer;
  @include transition;
  &:before, &:after {
    display: none;
  }
  &:hover {
    background: $primary-950;
  }
}
.noUi-horizontal .noUi-handle {
  width: 24px;
  height: 24px;
  right: -8px;
  top: -8px;
}