
.custom-range__calc {
  display: flex;
  padding: 0 0 12px 0;
  margin: 0;
  li {
    margin: 0 40px 12px 0;
    &:last-child {
      margin-right: 0;
    }
  }
  .h1 {
    margin-bottom: 2px;
  }
  .project-data_note {
    padding: 0;
    margin: 0;
  }
}

.calc-valuta {
  padding-bottom: 8px;
  span {
    font-size: 13px;
    line-height: 20px;
    color: $black-500;
    margin: 0 16px 8px 0;
    vertical-align: top;
  }
}

.calc-income {
  margin-bottom: 16px;
  &:last-of-type {
    margin-bottom: 24px;
  }
  p {
    margin-bottom: 0;
    span {
      margin-right: 16px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .text--13 {
    margin-top: 4px;
    color: $black-400;
  }
}

.calc-head {
  @include horizonCenterBetween;
  flex-direction: row-reverse;
  @media (max-width: $nodesk) {
    flex-wrap: wrap;
    flex-direction: row;
  }
  & > * {
    margin-bottom: 20px;
    margin-right: 16px;
    &:last-of-type {
      margin-right: 0;
    }
    @media (max-width: $nodesk) {
      margin-right: 0;
    }
  }
  &__col {
    @include horizonCenter;
    flex: 1;
  }
  .form-control {
    width: 160px;
    margin-right: 16px;
    @media (max-width: $sm) {
      width: 140px;
    }
  }
  .text--13 {
    color: $black-400;
  }
}

.slider-condition {
  margin-bottom: 32px;
  .swiper-slide {
    width: 200px;
    background: $white;
    box-shadow: $shadow-small;
    border-radius: 8px;
  }
  &__title {
    padding: 12px 16px;
    color: $black-500;
  }
  &__head {
    display: flex;
    padding: 4px 16px;
    background-color: $primary-100;
    span {
      width: 33.3%;
      font-weight: 500;
      font-size: 11px;
      line-height: 16px;
    }
  }
  &__content {
    padding: 4px 16px;
    li {
      padding: 4px 0;
      display: flex;
      flex-wrap: wrap;
      p {
        margin: 0 0 4px 0;
        font-size: 13px;
        line-height: 20px;
        color: $black-400;
        width: 100%;
      }
      span {
        width: 33.3%;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
  .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
    opacity: 0;
  }
  &--not-stage {
    .swiper-slide {
      width: 170px;
    }
    .slider-condition {
      &__title {
        padding-bottom: 4px;
      }
      &__content {
        span {
          width: 100%;
        }
      }
    }
  }
}
