
.form-support {
  margin-bottom: 56px;
  margin-top: 24px;
  @media (max-width:$md) {
    margin-top: 0;
    margin-bottom: 32px;
  }
  .form-control {
    background-color: $white;
  }
}

.social-messenger {
  &__link {
    margin-bottom: 12px;
    @include horizonCenter;
    padding-left: 52px;
    height: 40px;
    font-size: 20px;
    line-height: 28px;
    color: $black-500;
    span {
      border-radius: 50%;
      width: 40px;
      height: 40px;
      @include verticalCenter;
      color: $white;
      font-size: 24px;
      position: absolute;
      top: 0;
      left: 0;
    }
    &:hover {
      color: $primary-900;
    }
  }
}

.support--svg {
  max-width: 100%;
}

.block--support {
  .support-img {
    margin-bottom: 60px;
    @media (max-width: $md) {
      width: 233px;
    }
  }
  .btn {
    margin-bottom: 44px;
    width: 320px;
    max-width: 100%;
  }
  .h3 {
    text-align: center;
    color: $black-500;
  }
  .social-messenger {
    @include horizonCenter;
    &__link {
      margin: 0 12px;
    }
  }
}

