
.swiper-team {
  .swiper-slide {
    width: 248px;
  }
  @media (min-width:$nodesk) {
    .swiper-slide {
      width: 33.3%;
      margin-left: 0;
      margin-right: 0;
      padding-left: 12px;
      padding-right: 12px;
    }
    .swiper-wrapper {
      margin-left: -12px;
      margin-right: -12px;
    }
  }
  .swiper-button {
    margin-top: -34px;
    &-next {
      right: -8px;
    }
    &-prev {
      left: -8px;
    }
  }
}

.team-preview {
  width: 100%;
  padding: 12px;
  background-color: $white;
  min-height: 100%;
  border-radius: 8px;
  box-shadow: $shadow-small;
  &__img {
    width: 100%;
    overflow: hidden;
    border-radius: 2px;
    max-height: 300px;
    margin-bottom: 20px;
    &:before {
      content: '';
      padding-top: 100%;
      display: block;
    }
    img {
      @include image-size;
      position: absolute;
      top: 0;
      left: 0;
      object-position: center;
    }
  }
  &__name {
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 20px;
    span {
      display: block;
      font-size: 13px;
      line-height: 20px;
      color: $black-500;
      font-weight: 400;
    }
  }
  p {
    line-height: 24px;
  }
}


