
.ipo {
  display: flex;
  justify-content: space-between;
  @media (max-width:$md) {
    display: block;
  }
  &-info {
    width: 184px;
    padding: 16px;
    border-radius: 8px;
    background: $gradient-900;
    color: $white;
    a:not(.btn), button:not(.btn), input:not(.btn) {
      color: $white;
    }
    .nice-select {
      padding: 0 20px 0 0;
      height: auto;
      line-height: 20px !important;
      font-size: 13px;
      background-color: transparent;
      border-radius: 0;
      border: 0;
      font-weight: 700;
      display: inline-block;
      width: auto;
      .current {
        color: $white;
      }
      &:after {
        border-color: $white;
        right: 0;
        margin-top: -5px;
      }
      .list {
        width: initial;
        max-width: 240px;
      }
      .option {
        padding: 5px 10px;
        white-space: nowrap;
        overflow: hidden;
      }
    }
    @media (max-width:$md) {
      width: 100%;
    }
  }
  &-list {
    padding: 0;
    margin: 0;
    li {
      color: $primary-300;
      font-size: 13px;
      & + li {
        margin-top: 24px;
      }
    }
    &__number {
      display: block;
      color: $white;
      margin-bottom: 4px;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
    }
    .tooltip-info {
      font-size: 16px;
      color: $primary-700;
    }
  }
  &-chart {
    width: calc(100% - 184px - 24px);
    border: 1px solid $primary-300;
    border-radius: 8px;
    padding: 0 16px;
    background-color: $white;
    @media (max-width:$md) {
      display: none;
    }
    h4 {
      padding-top: 24px;
      .tooltip-info {
        margin-top: -4px;
      }
    }
  }
}

