
.stage-slider {
  @media (min-width: $nodesk) {
    .swiper-wrapper {
      display: block;
      margin: 0;
    }
    .swiper-slide {
      width: 100%;
      height: auto;
      margin-bottom: 16px;
    }
  }
}

.stage {
  background-color: $white;
  display: flex;
  padding: 24px;
  border-radius: 8px;
  @media (max-width:$nodesk) {
    display: block;
    padding: 12px;
  }
  .project-preview__chart {
    position: absolute;
    left: 0;
    top: 0;
    @media (max-width:$nodesk) {
      position: relative;
      margin: 16px auto;
      top: auto;
      left: auto;
      width: 100px;
      height: 100px;
    }
  }
  &-info {
    width: calc(100% - 100px);
    padding-left: 124px;
    min-height: 100px;
    @media (max-width:$nodesk) {
      width: 100%;
      padding: 0;
    }
  }
  .badge {
    margin-bottom: 8px;
    @media (max-width:$nodesk) {
      width: 100%;
    }
  }
  h3 {
    margin-bottom: 20px;
    @media (max-width:$nodesk) {
      text-align: center;
      margin-bottom: 16px;
      font-size: 18px;
      line-height: 24px;
    }
  }
  hr {
    display: block;
    background: $primary-300;
    height: 1px;
    border: 0;
    margin: 20px 0;
    border-radius: 8px;
    @media (max-width:$nodesk) {
      display: none;
    }
  }
  .project-data {
    margin: 0;
  }
  .project-preview__sum {
    padding: 0;
    margin: 0;
    min-height: auto;
    @media (max-width:$nodesk) {
      width: 100%;
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: center;
      margin-bottom: 8px;
    }
    &:before {
      display: none;
    }
    & + .project-data_note {
      margin-top: 0;
      @media (max-width:$nodesk) {
        display: none;
      }
    }
  }

  &.stage--expect {
    opacity: .5;
    .ldBar path.mainline {
      opacity: 0;
    }
  }

  .js--slidetoggle {
    @media (max-width: $md) {
      width: 100%;
      text-align: center;
      border-radius: 4px;
      background-color: $primary-200;
      padding: 6px 12px;
      span {
        padding-left: 20px;
      }
    }
  }
}


