
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background: rgba($black,.5);
  pointer-events: none;
  opacity: 0;
  transition: .5s;
  &.active {
    opacity: 1;
    pointer-events: auto;
  }
}

.panel {
  display: none;
  position: fixed;
  margin: 8px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 12;
  &-list {
    background-color: $white;
    box-shadow: $shadow-large;
    border-radius: 8px;
    width: 100%;
    display: flex;
  }
  &-item {
    flex-grow: 1;
    height: 56px;
    font-weight: 500;
    font-size: 11px;
    line-height: 12px;
    text-align: center;
    color: $black-500;
    @include verticalCenter;
    .icon {
      width: 24px;
      height: 24px;
      display: block;
      margin: 0 auto 4px auto;
      transition: .5s;
    }
    &:hover, &.active {
      color: $primary-900;
    }
  }
  @media (max-width:$lg) {
    display: block;
  }
}

.mobile-menu {
  bottom: 100%;
  margin: 8px 0;
  left: 0;
  right: 0;
  background-color: $white;
  box-shadow: $shadow-large;
  border-radius: 8px;
  position: absolute;
  padding: 8px 0;
  transition: .5s;
  //transform: scale(0);
  transform: translateY(-35px);
  opacity: 0;
  pointer-events: none;
  transform-origin: bottom right;
  &.active {
    opacity: 1;
    pointer-events: auto;
    //transform: scale(1);
    transform: translateY(0);
  }
  &__list {
    width: 100%;
  }
  &__link {
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    padding: 8px 16px 8px 52px;
    min-height: 40px;
    width: 100%;
    display: flex;
    align-content: center;
    align-items: center;
    .icon {
      position: absolute;
      left: 16px;
      top: 50%;
      width: 24px;
      height: 24px;
      margin-top: -12px;
      z-index: 1;
      color: $black-400;
      transition: .5s;
    }
    &:hover {
      color: $primary-900;
      .icon {
        color: $primary-900;
      }
    }
  }
}





