
.ipo-chart--analytics {
  canvas {
    max-height: 210px;
  }
}

.ipo-analytics--title {
  display: none;
  @media (max-width:$md) {
    display: block;
  }
}

.analytics-row {
  display: flex;
  align-items: center;
  align-content: center;
  @media (max-width: $md) {
    display: block;
  }
}

.analytics-chart {
  width: 168px;
  height: 168px;
  @media (max-width: $md) {
    margin: 0 auto 24px auto;
  }
}

.analytics-info {
  padding-left: 60px;
  width: 680px;
  max-width: 100%;
  color: $black-500;
  &__title {
    width: 280px;
    max-width: 100%;
  }
  td {
    padding: 10px 4px;
  }
  @media (max-width: $md) {
    padding: 0;
    width: 100%;
    table {
      display: block;
      tbody {
        display: block;
        tr {
          width: 100%;
          padding: 8px 0 0 36px;
          display: flex;
          flex-wrap: wrap;
          & > * {
            margin-right: 16px;
            &:last-child {
              margin-right: 0;
            }
            &:first-child {
              width: 100%;
            }
          }
        }
        td {
          padding: 2px 0;
        }
      }
    }
  }
}

.analytics-data {
  padding-left: 36px;
  display: inline-block;
  &__icon {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
  }
  @media (max-width: $md) {
    padding-left: 0;
    .analytics-data__icon {
      left: -36px;
    }
  }
}

