
.main-entry {
  display: flex;
  min-height: 100vh;
  &__col {
    width: 50%;
    display: flex;
    &:after {
      content: '';
      display: block;
      clear: both;
    }
  }
  &__bg {
    background-color: $primary-900;
    justify-content: flex-end;
    align-content: center;
    align-items: center;
    .main-entry__content {
      text-align: center;
    }
    .main-form--svg {
      max-width: 100%;
      max-height: calc(100vh - 32px);
    }
  }
  &__form {
    background-image: url(../img/main-form/bg-form.png);
    background-color: $primary-100;
    background-position: center left;
    background-repeat: no-repeat;
    align-items: center;
    justify-content: flex-start;
  }
  &__content {
    width: 750px;
    max-width: 100%;
    padding: 16px;
  }

  @media (max-width: $md) {
    &__bg {
      display: none;
    }
    &__col {
      width: 100%;
    }
    &__form {
      justify-content: center;
      align-content: center;
      align-items: center;
      background-image: url(../img/main-form/bg-form_mob.png);
      background-position: top center;
    }
  }
  
}

.main-form {
  width: 420px;
  max-width: 100%;
  margin: 0 auto;
  padding: 40px 16px;
  background: $white;
  box-shadow: $shadow-small;
  border-radius: 8px;
  h1, .h1 {
    text-align: center;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .subtitle {
    padding-top: 12px;
    color: $black-500;
    .link-primary {
      font-weight: 400;
    }
  }
  .form-group {
    margin-bottom: 12px;
    & + .form-check {
      padding-top: 4px;
    }
  }
  .btn {
    &:nth-of-type(1) {
      margin-top: 28px;
    }
  }
  .text {
    text-align: center;
    margin-bottom: 28px;
  }
}

.tf-auth {
  display: flex;
  margin-left: -6px;
  margin-right: -6px;
  .form-control {
    flex: 1;
    margin: 0 6px;
    text-align: center;
    padding: 0;
  }
  @media (max-width: 350px) {
    margin-left: -4px;
    margin-right: -4px;
    .form-control {
      margin: 0 4px;
      text-align: center;
    }
  }
}

.tf-auth--repeat {
  text-align: center;
  margin-bottom: 28px;
  font-size: 13px;
  color: $black-500;
}




