
.content {
  background-color: $white;
  padding: 24px;
  margin-bottom: 32px;
  border-radius: 4px;
  box-shadow: $shadow-small;
  @media (max-width:$md) {
    margin-bottom: 28px;
  }
  &--overflow {
    overflow: hidden;
  }
  &--table {
    padding: 0 !important;
    //box-shadow: $shadow-large;
    @media (max-width:$lg) {
      background-color: transparent;
      box-shadow: none;
    }
  }
  &--resp-bg-transparent {
    @media (max-width:$lg) {
      padding: 0 !important;
      box-shadow: none;
      background-color: transparent;
    }
  }
  &--head {
    padding: 22px 24px;
    @include horizonCenterBetween;
    &__item {
      padding-right: 12px;
      @include horizonCenter;
      & > * {
        margin: 6px 12px 6px 0;
        &.nav {
          margin-left: 20px;
          @media (max-width: $lg) {
            margin-left: 0;
          }
        }
        &:last-child {
          margin-right: 0;
        }
      }
      @media (max-width: $lg) {
        padding-right: 0;
        flex-wrap: wrap;
        width: 100%;
      }
      & + .content-form__hr {
        margin-top: 16px;
        margin-bottom: 16px;
      }
    }
    @media (max-width:$md) {
      padding-top: 14px;
      padding-bottom: 14px;
    }
    .btn {
      @media (max-width:$lg) {
        display: none;
      }
    }

    & + .content--tightly {
      margin-top: -32px;
    }
  }
  @media (max-width:$md) {
    padding-left: 16px;
    padding-right: 16px;
    margin-bottom: 28px;
    .content-form__hr {
      margin-right: -16px;
      margin-left: -16px;
      width: calc(100% + 16px*2);
    }
  }
  &-form {
    width: 640px;
    max-width: 100%;
    .form-group {

    }
    .btn {
      margin-top: 4px;
    }
    & > *:last-child {
      margin-bottom: 0;
    }
    &__hr {
      margin: 32px -24px;
      width: calc(100% + 24px*2);
      height: 1px;
      opacity: 1;
      background: $primary-300;
    }
  }
  & > *:last-child {
    margin-bottom: 0;
  }
  &--reset {
    padding: 0;
    background-color: transparent;
  }
  &--tightly {
    margin-bottom: 1px;
  }
}

.page-back {
  font-size: 24px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-top: 3px;
  &:hover {
    color: $primary-950;
  }
  & + * {
    padding-left: 36px;
  }
}

.block {
  overflow: hidden;
  padding: 64px 24px;
  &:nth-child(odd){
    padding-top: 40px;
    padding-bottom: 40px;
    background: $white;
  }
  &-accordion {
    @media (max-width:$md-tablet) {
      display: none;
      margin-top: 16px;
      height: 0;
      overflow: hidden;
      @include transition;
      &.show-on-mob {
        display: block;
        height: auto;
        overflow: visible;
      }
    }
    .nav {
      z-index: 2;
      width: auto;
      margin-top: -4px;
      &-link:not(.active) {
        color: $black;
      }
      @media (max-width:$md-tablet) {
        margin-top: 0;
      }
    }
  }
  @media (max-width:$md-tablet) {
    padding: 24px 16px !important;
  }
}

.js--open-block {
  @media (max-width:$md-tablet) {
    padding-right: 24px;
    margin-bottom: 0;
  }
  .icon {
    position: absolute;
    top: 6px;
    right: 0;
    width: 18px;
    height: 18px;
    display: none;
    @include transition;
    font-size: 14px;
    z-index: 2;
    @media (max-width:$md-tablet) {
      display: inline-block;
    }
  }
  &.active {
    .icon {
      transform: scaleY(-1);
    }
  }
}

