
.header {
  background-color: $primary-900;
  color: $white;
  overflow: hidden;
  padding-bottom: 60px;
  &.header-v2 {
    padding-bottom: 0;
    & + main > .container {
      margin-top: 0;
      padding-top: 40px;
    }
  }
  @media (max-width:$lg) {
    padding-bottom: 56px;
  }
  a, buuton, input {
    color: $white;
  }
  .container {
    max-width: 1212px;
    display: flex;
    justify-content: space-between;
  }
  &-logo {
    width: 220px;
    padding: 16px 0;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      width: 2000px;
      height: 380px;
      background: $primary-800;
      border-radius: 160px;
      transform: translateY(-50%);
      margin-top: 60px;
      @media (max-width:$md) {
        display: none;
      }
    }
  }
  &-item {
    padding: 22px 0;
    @include horizonCenter;
    @media (max-width:$lg) {
      padding: 16px 0 20px;
    }
  }
  .nav {
    width: auto;
    flex-wrap: nowrap;
    background: transparent;
    border-radius: 0;
    padding: 0;
    &-item {
      @media (max-width: $xl) {
        margin-right: 0;
      }
    }
    &-link {
      background-color: transparent;
      &.active {
        background-color: transparent;
        color: $white-900;
        opacity: .8;
      }
      &:focus {
        color: $white;
      }
      &:hover:not(.active) {
        background-color: $white;
        color: $primary-900;
      }
    }
    @media (max-width:$lg) {
     display: none;
    }
  }
  &-button {
    margin-left: $indent;
    @include horizonCenter;
  }
  &-user {
    //margin-left: $indent-sm;
    margin-left: 8px;
    @media (max-width:$lg) {
      margin-left: 12px;
    }
    .icon-btn {
      display: none;
      @media (max-width:$lg) {
        display: flex;
      }
    }
    &__name {
      font-weight: 500;
      padding: 6px 8px;
      border-radius: 32px;
      &:hover {
        background-color: $primary-700;
      }
      &:active {
        background-color: $primary-950;
      }
      @media (max-width:$lg) {
        display: none;
      }
    }
  }
}

.logo {
  img {
    width: 180px;
    @media (max-width:$lg) {
      width: 140px;
    }
    @media (max-width:$md) {
      width: 112px;
    }
  }
}

.btn-output {
  display: none;
  font-weight: 500;
  font-size: 13px;
  line-height: 20px;
  padding: 6px 16px;
  background: $primary-800;
  border-radius: 4px;
  &:hover {
    background-color: $primary-700;
  }
  &:active {
    background-color: $primary-950;
  }
  @media (max-width:$lg) {
    display: inline-block;
    margin-right: 12px;
  }
}