

//button

.btn {
  font-weight: 500;
  line-height: 24px;
  padding: 8px 24px;
  border-radius: 4px;
  box-shadow: none !important;
  outline: none !important;
  @include transition;
  &:not(.btn-light) {
    color: $white;
    &:hover,
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active,
    &:active {
      color: $white;
    }
  }
  &-block {
    width: 100%;
    display: block;
    &-mob {
      @media (max-width: $nodesk) {
        width: 100%;
        display: block;
      }
    }
  }
  &-primary {
    color: $white;
    background-color: $primary-900;
    &:hover {
      color: $white;
      background-color: $primary-800;
    }
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active,
    &:active {
      color: $white;
      background-color: $primary-950;
    }
  }
  &-light {
    background-color: $primary-200;
    color: $primary-900;
    &:hover {
      color: $white;
      background-color: $primary-700;
    }
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active,
    &:active {
      color: $white;
      background-color: $primary-950;
    }
  }
  &:not(.btn-outline) {
    border: none !important;
  }
  &.btn-sm {
    font-size: 13px;
    line-height: 20px;
    padding: 6px 16px;
  }
  &.btn-lg {
    padding: 12px 24px;
  }
  &.btn-mob-full {
    @media (max-width: $md) {
      width: 100%;
    }
  }

  &.btn--width{
    min-width: 160px;
    &-sm {
      min-width: 108px;
    }
    @media (max-width: $nodesk) {
      width: 100%;
      max-width: -webkit-fill-available;
    }
  }
}
.btn-warning {
  color: $white !important;
}
.btn-block+.btn-block {
  margin-top: 12px;
}


