.media-preview {
  display: block;
  min-height: 100%;
  color: $primary-950;
  &__poster {
    width: 100%;
    overflow: hidden;
    max-height: 332px;
    margin-bottom: 12px;
    border-radius: 8px;
    &:before {
      content: '';
      padding-top: 65%;
      display: block;
    }
    img {
      @include image-size;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__name {
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    display: -moz-box;
    -moz-box-orient: vertical;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    line-clamp: 1;
    box-orient: vertical;
  }
  &:hover {
    color: $primary-700;
  }
}




