

.table {
  --bs-table-striped-color: #616980;
  --bs-table-striped-bg:  #F8FAFC;
}
.table-striped>tbody>tr:nth-of-type(odd) {
  --bs-table-accent-bg: $white;
  color: #616980;
}
.table-striped>tbody>tr:nth-of-type(even) {
  --bs-table-accent-bg: #F8FAFC;
  color: #616980;
}
.table>:not(caption)>*>* {
  @media (max-width:$nodesk) {
    background-color: transparent;
    box-shadow: none;
  }
}

.table--my-theme {
  width: 100%;
  font-size: 13px;
  line-height: 20px;
  color: $black-500;
  border-radius: 4px;
  background-color: $white;
  @media (max-width:$nodesk) {
    background-color: transparent;
  }
  .table {
    width: 100%;
    font-size: 13px;
    line-height: 20px;
    color: $black-500;
    @media (max-width:$nodesk) {
      display: block;
    }
  }
  tr {
    vertical-align: middle;
  }
  thead {
    box-shadow: $shadow-medium;
    tr {
      &:first-child {
        th {
          &:first-child {
            border-top-left-radius: 4px;
          }
          &:last-child {
            border-top-right-radius: 4px;
          }
        }
      }
    }
    @media (max-width:$nodesk) {
      display: none;
    }
  }
  tbody {
    @media (max-width:$nodesk) {
      display: block;
    }
    &:before {
      content:"";
      display: block;
      height: 12px;
      @media (max-width:$nodesk) {
        display: none;
      }
    }
    &:after {
      content:"";
      display: block;
      height: 8px;
      @media (max-width:$nodesk) {
        display: none;
      }
    }
    tr {
      &:last-child {
        &:first-child {
          border-bottom-left-radius: 4px;
        }
        &:last-child {
          border-bottom-right-radius: 4px;
        }
      }
      @media (max-width:$nodesk) {
        display: block;
        padding: 8px 16px;
        background-color: $white !important;
        border-radius: 4px;
        & + tr {
          margin-top: $indent-sm;
        }
      }
    }
  }
  th, td {
    padding: 14px 24px;
    border-width: 0;
  }
  th {
    text-align: left;
    font-weight: bold;
  }
  td {
    font-weight: normal;
    @media (max-width:$nodesk) {
      display: block;
      text-align: right;
      padding: 2px 0;
      margin: 8px 0;
      &:before {
        content: attr(data-label);
        white-space: nowrap;
        float: left;
        padding-right: 8px;
        text-align: left;
        color: $black-900;
      }
    }
  }
  //.badge {
  //  width: 100%;
  //  text-align: center;
  //}
  &.table--nolabel {
    td {
      @media (max-width: $nodesk) {
        text-align: left;
        margin: 4px 0;
        &:before {
          display: none;
        }
      }
    }
  }

  @media (max-width: $nodesk) {
    .btn {
      & + .btn, & + .badge {
        margin-top: 12px !important;
      }
    }
    .badge {
      & + .badge, & + .btn {
        margin-top: 12px !important;
      }
    }
  }

}

.cell-sm {
  width: 220px;
  @media (max-width:$nodesk) {
    width: 100%;
  }
}

.table--scroll-mob {
  @media (max-width: $nodesk) {
    .table {
      width: calc(100% + 32px);
      margin-left: -16px;
      margin-right: -16px;
      padding: 0 8px;
    }
    tbody {
      display: flex;
      flex-wrap: nowrap;
      overflow: hidden;
      overflow-x: auto;
      justify-content: left;
      padding: 40px;
      margin: -40px;
    }
    tr {
      width: 240px;
      margin: 0 8px !important;
      flex: 0 0 auto;
      padding: 18px 16px 4px 16px;
      box-shadow: $shadow-small;
    }
  }
}

.table {
  &--title {
    &:not(.color-primary) {
      color: $black-900;
    }
    @media (max-width:$nodesk) {
      width: 100%;
      text-align: left;
      font-weight: 500;
    }
  }
}


.table-text {
  width: initial;
  border: none;
  @media (max-width:$nodesk) {
    display: block;
  }
  tbody {
    @media (max-width:$nodesk) {
      display: block;
    }
  }
  tr {
    border: none;
    @media (max-width:$nodesk) {
      display: block;
      padding: 4px 0;
    }
  }
  td {
    padding: 4px 8px 4px 0;
    border: none;
    @media (max-width:$nodesk) {
      display: block;
      padding: 0;
    }
  }
  .h4 {
    margin: 0;
    color: $black-500;
  }
}

.table-large {
 @media (min-width: $nodesk + 1px) {
   th, td {
     &:not(:first-of-type) {
       padding-left: 8px;
     }
     &:not(:last-of-type) {
       padding-right: 8px;
     }
   }
 }
}

.table--calc {
  margin-top: 24px;
  @media (min-width: $nodesk + 1px) {
    th, td {
      padding-left: 12px;
      padding-right: 12px;
    }
    td {
      padding-top: 6px;
      padding-bottom: 6px;
    }
  }
  .form-control {
    height: 24px;
    padding: 0 8px;
  }
}