
//normalize

* {position:relative;margin:0;padding:0;box-sizing:border-box;}
a,button,span {display:inline-block}
input,textarea {display:block;width:100%}
table {width:100%;border-collapse:collapse}
a {
  background-color:transparent;
  text-decoration:none;
  &:hover {
    text-decoration: none;
  }
}
img {border:none;max-width:100%;height:auto}
a,button {outline:0;cursor:pointer}
li {list-style:none}
button {background:0 0;border:none;box-shadow: none !important;outline: none !important;}
a,button,input,textarea {color:$black-900;font-weight:inherit;-webkit-appearance:none;transition:all .5s ease}
:after,:before{box-sizing:border-box}
textarea {resize:none;outline:0;-webkit-appearance:none;background:0 0}
input {outline:0;-webkit-appearance:none;background:0 0}
input[type=number] {-moz-appearance:textfield}
input::-webkit-inner-spin-button,input::-webkit-outer-spin-button {-webkit-appearance:none}
input[type=submit] {cursor:pointer;-webkit-appearance:none;display:inline-block}
input[type=search]::-webkit-search-cancel-button {background:0 0;display:none}
.clearfix {&:after{content: '';clear: both;display: block}}


//body

html {
  height: 100%;
}

body {
  font-family: $font;
  color: $black-900;
  font-style: normal;
  font-weight: 400;
  background: $primary-100;
  font-size: 16px;
  width: 100%;
  &.body--overflow {
    height: 100%;
    overflow: hidden;
  }
}

.wrapper {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  width: 100%;
}
main {
  flex: auto;
  display: block;
  & > .container {
    margin-top: -48px;
  }
}
header+main {
  padding-bottom: 32px;
  @media (max-width:$lg) {
    padding-bottom: 60px;
  }
}
footer {
  margin: auto auto 0 auto;
  width: 100%;
}

.container,
.container-fluid {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
  z-index: 2;
}

.container {
  max-width: 1132px;
}
.container-fluid {
  max-width: 1920px;
}


