
.document {
  &-list {

  }
  &-file {
    display: block;
    margin-bottom: $indent-sm;
    line-height: 24px;
    padding-left: 36px;
    font-weight: 500;
    color: $primary-900;
    .icon {
      width: 24px;
      height: 24px;
      position: absolute;
      top: 0;
      left: 0;
    }
    &:hover {
      color: $primary-700;
    }
    &[disabled] {
      pointer-events: none;
      opacity: .5;
    }
  }
}

.contract-block {
  padding: 16px;
  border-radius: 4px;
  border: 1px solid $primary-300;
  margin-bottom: 12px;
  @include horizonCenterBetween;
  width: 100%;
  &:hover {
    border-color: $primary-950;
  }
  @media (max-width:$md) {
    flex-wrap: wrap;
  }
  &-sm {
    font-size: 13px;
  }
  &[disabled] {
    pointer-events: none;
    opacity: .7;
  }
}


.wrap-icon-success {
  .icon {
    width: 104px;
    height: 104px;
    margin-bottom: 12px;
    color: $green;
    @media (max-width:$md) {
      width: 80px;
      height: 80px;
    }
  }
}

