
.account-selection {
  padding: 12px 24px;
  background: $primary-200;
  border-radius: 4px;
  margin-bottom: 12px;
  @include horizonCenterBetween;
  @media (max-width: $md-tablet) {
    display: block;
  }
  &:last-of-type {
    margin-bottom: 0;
  }
  &__data {
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    color: $black-500;
    padding-left: 32px;
    min-height: 24px;
    padding-top: 3px;
    .icon {
      width: 24px;
      height: 24px;
      color: $primary;
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -12px;
    }
  }
  &__number {
    font-size: 13px;
    line-height: 20px;
    color: $black-400;
    white-space: nowrap;
    overflow: hidden;
    @media (max-width: $md-tablet) {
      margin-top: 8px;
    }
  }
}
