
.statistics-investor {
  display: flex;
  margin-left: 0 !important;
  padding-left: 0 !important;
  li {
    padding: 0 40px;
    border-right: 1px solid $primary-300;
    &:first-child {
      //padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
      border-right: none;
    }
  }
  span {
    display: block;
    font-weight: 500;
    font-size: 20px;
  }
  .text-note {
    color: $black-400;
  }

  @media(max-width:$md) {
    display: block;
    li {
      padding: 0;
      margin: 12px 0;
      border: 0;
      &:first-child {
        margin-top: 0;
      }
    }
  }

}

.phase-slider {
  .swiper-container {
    overflow: visible;
  }
  .swiper-slide {
    width: 162px;
    font-size: 13px;
    height: auto;
    padding: 12px 16px;
    background: $white;
    border: 1px solid $primary-300;
    border-radius: 8px;
    @media (max-width: $nodesk) {
      border: none;
    }
  }
  .text-note {
    margin: 4px 0;
  }
  .badge {
    width: 100%;
  }
  .swiper-button-disabled {
    opacity: 0 !important;
  }
  @media (max-width:735px) {
    .swiper-wrapper {
      display: block;
    }
    .swiper-slide {
      width: 100%;
      margin-bottom: 16px;
    }
    .swiper-button {
      display: none;
    }
  }
}